@use "../base/variables" as v;
@use "../base/font" as f;

svg {
  pointer-events: none;
}

.card {
  background-color: white;
  // width: 72rem;
  width: clamp(33rem, 72rem, 80%);
  margin: 35rem auto;
  display: flex;
  border-radius: 1rem;
  position: relative;
  box-shadow: 0 2rem 3rem 0.25rem v.$shadow-1;

  &_img {
    width: 29rem;
    object-fit: cover;
    object-position: left;
    border-radius: 1rem 0 0 1rem;
  }

  &_article {
    padding: 3.5rem 4rem;
  }

  &_h1 {
    font-size: 2rem;
    color: v.$vd-grayish-blue;
    margin-bottom: 1.5rem;
  }

  &_p {
    font-size: 1.3rem;
    margin-bottom: 2rem;
    color: v.$dg-blue;
    line-height: 2rem;
  }

  &_profile {
    display: flex;
    align-items: center;

    &-img {
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      object-fit: contain;
      margin-right: 1.8rem;
    }
  }

  &_h2 {
    font-size: 1.3rem;
    color: v.$vd-grayish-blue;
  }

  &_date {
    font-size: 1.3rem;
    color: v.$g-blue;
  }
}

//! Share Icon start

.share_popup {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  position: relative;
  background: v.$l-blue;
}

.share_popup:focus,
.share_popup:hover .icon,
.share_popup:hover {
  fill: white;
  background: v.$vd-grayish-blue;
}

// Share icon end

.share_popup {
  margin-left: auto;
  z-index: 2;
}

.share_popup-content {
  display: none;
}

.share_popup:focus-within .share_popup-content {
  width: 24.5rem;
  border-radius: 1rem;
  background-color: v.$vd-grayish-blue;
  padding: 1.4rem 2rem;
  position: absolute;
  top: -8rem;
  right: -11rem;
  box-shadow: 0 2rem 3rem 0.25rem v.$shadow-1;
  opacity: 1;
  color: v.$g-blue;
  text-transform: uppercase;
  letter-spacing: 0.7rem;
  font-size: 1.3rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  cursor: auto;

  &::before {
    content: "";
    background-color: v.$vd-grayish-blue;
    position: absolute;
    left: 45%;
    top: 80%;
    width: 2rem;
    height: 2rem;
    rotate: 45deg;
  }
}

.attribution {
  text-align: center;
  background-color: v.$g-blue;
  padding: 1rem;
}

@media only screen and (max-width: 43.75em) {
  .card {
    margin-top: 22rem;
    flex-direction: column;
    position: relative;

    &_img {
      border-radius: 1rem 1rem 0 0;
      width: 100%;
      object-fit: cover;
      object-position: center;
    }

    &_article {
      padding: 3.5rem 3.5rem 1.5rem 3.5rem;
    }

    &_h1 {
      font-size: 1.6rem;
      color: v.$vd-grayish-blue;
    }

    &_p {
      font-size: 1.3rem;
    }
  }

  .share_popup:focus {
    background-color: red;
  }

  .share_popup:focus-within .share_popup-content {
    width: clamp(33rem, 80vw, 80vw);
    border-radius: 0 0 1rem 1rem;
    background-color: v.$vd-grayish-blue;
    padding: 2.2rem 3.5rem;
    position: absolute;
    top: -1.6rem;
    right: -3.5rem;
    box-shadow: 0 2rem 3rem 0.25rem v.$shadow-1;
    opacity: 1;
    color: v.$g-blue;
    text-transform: uppercase;
    letter-spacing: 0.7rem;
    font-size: 1.3rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: auto;
    z-index: -2;

    &::before {
      content: "";
      display: none;
    }
  }

  .icon {
    margin-left: 2rem;
  }
}
