@use 'variables' as v;

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: 'Manrope', sans-serif;
  background-color: v.$l-blue;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

a:visited,
a:active {
  color: v.$vd-grayish-blue;
}

button, input[type="submit"], input[type="reset"] {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}